import React, {Component} from "react";

class Fold extends Component{
    
    render(){
        const items = this.props.foldViewImages[0]['viewimages'].map((item,i) => {
            if(this.props.productType === "2piece" && item.type === "vest"){
                return false;
            }
            else if(this.props.productType === "jacket" && item.type === "vest"){
                return false;
            }else if(this.props.productType === "vest" && item.type === "jacket"){
                return false;
            }else if(this.props.productType === "pant" && (item.type === "jacket" )){
                return false;
            }

            return (
               <img key={item.id} src={item.img} style={{ display: `${item.isActive?'block':'none'}`}} alt=""/>
            )
        });

        return (
            <div id="Inside" className={`${this.props.actView === 'Inside'?'currentView':''}`}>
              {items}
            </div>
        )
    }
}

export default Fold;