import React from 'react';

class Controls extends React.Component {

   handleFabricDePage(){
     this.props.handleFabricPage()
   }

   handlePantUpDown(){
     this.props.handlePantUpDown();
   }
   handleMannquin(){
     this.props.handleMannquin();
   }
   handleJacketHShow(){
    this.props.handleJacketHShow();
  }

  handleZoomArea(){
    this.props.handleZoomArea();
  }


  render(){
      
    return (
      <div className="view-nav">
        <ul className="clearfix">
            <li><a title="Zoom" onClick={this.handleZoomArea.bind(this)}><i className="icon-icon-112"></i></a></li>
            <li><a title="Hide" className="vt-jk" onClick={this.handleJacketHShow.bind(this)}><i className={`${this.props.isJacket ?'suit-icon-Show_Jacket':'suit-icon-Hide_Jacket'}`}></i></a></li>
            <li><a className={`up-down ${this.props.upDownView ?'up-down-this':''}`} onClick={this.handlePantUpDown.bind(this)}><i className="icon-icon-114"></i></a></li>
            <li><a title="Mannequin" onClick={this.handleMannquin.bind(this)}><i className={`${this.props.isMannquin ?'suit-icon-Manequinn_Show':'suit-icon-Manequinn_Hide'}`}></i></a></li>
            <li><a className="fabric-frame" onClick={this.handleFabricDePage.bind(this)} title="Fabric"><i className="icon-icon-119"></i></a></li>
        </ul>
      </div>
    )
  }
}

export default Controls;