import React from 'react';

class Review extends React.Component {

    dismissModal() {
        this.props.toggle(this.props.showReviewModal);
    }

    render(){
        
       return (
        <div onClick={this.dismissModal.bind(this)} className={`modal fade ${this.props.showReviewModal ?'in show':''}`} stye={{ display: `${this.props.showModal ?'block':'none'}`}} id="review-my-design" tabIndex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 className="modal-title">Review Design</h4>
                    </div>
                    
                    <div className="modal-body">
                        <div className="table-info">
                            <div className="table-responsive">
                                <table className="table" style={{ display: `${Object.keys(this.props.selectedJacketStyles).length?'block':'none'}`}}>
                                    <thead>
                                        <tr>
                                            <th>JACKET</th>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Object.keys(this.props.selectedJacketStyles).map((item, i) => (
                                            <tr key={i}><td>{this.props.selectedJacketStyles[item]['style'].toUpperCase()}</td><td>{this.props.selectedJacketStyles[item]['styleName']}</td><td><i className={this.props.selectedJacketStyles[item]['class']}></i><img src={this.props.selectedJacketStyles[item]['img']} /></td></tr>
                                        ))}
                                    </tbody>
                                </table>
                                {/* <hr></hr> */}
                                <table className="table" style={{ display: `${Object.keys(this.props.selectedPantStyles).length?'block':'none'}`}}>
                                    <thead>
                                        <tr>
                                            <th>Pant</th>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Object.keys(this.props.selectedPantStyles).map((item, i) => (
                                        
                                            <tr key={i}><td>{this.props.selectedPantStyles[item]['style'].toUpperCase()}</td><td>{this.props.selectedPantStyles[item]['styleName']}</td><td> <i className={this.props.selectedPantStyles[item]['class']}></i> <img src={this.props.selectedPantStyles[item]['img']} />  </td></tr>
    
                                        ))}
                                    </tbody>
                                </table>
                                
                                <table className="table" style={{ display: `${Object.keys(this.props.selectedVestStyles).length?'block':'none'}`}}>
                                    <thead>
                                        <tr>
                                            <th>Vest</th>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Object.keys(this.props.selectedVestStyles).map((item, i) => (
                                            <tr key={i}><td>{this.props.selectedVestStyles[item]['style'].toUpperCase()}</td><td>{this.props.selectedVestStyles[item]['styleName']}</td><td><i className={this.props.selectedVestStyles[item]['class']}></i> <img src={this.props.selectedVestStyles[item]['img']} /></td></tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                    
                    <div className="modal-footer">
                        <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                    </div>
                </div>
            </div>
        </div>
       )
   }
}

export default Review;