import React from 'react';
import AccentSubStyles from './AccentSubStyles';
import PerfectScrollbar from 'react-perfect-scrollbar';

class Accent extends React.Component { 

	constructor(props) {
		super(props);
		this.ulRef = React.createRef();
		
		this.state = {
			selectedStyle: {},
			isActive:true,
			passRef:'',
			selectedIndex: null,
		} 
	}

	categoryClick(e, item,i) {

		this.ulRef.current.classList.add('sml-nav-2');//adding class to parent element
		
		this.setState({
		  selectedStyle: item,
		  isActive:true,
		  selectedIndex : item.id,
		  passRef: this.ulRef
		})

		//trigger to parent comp fun
		this.props.categoryClick(item);
	}

	updateProduct(){
		//trigger to parent comp fun
		this.props.updateProduct();
	}

   render(){

	   let accentData = this.props.accentStyles;
	   let items;

	   items = accentData.map((item, i) => {
		  return (
			   <li onClick={(e) => this.categoryClick(e, item,i)}  key = {i} ><a className={this.state.selectedIndex == item.id ? 'active': ''}><i className={item.class}></i> <span>{item.name}</span></a></li>
		  )
		})

       return (
        <div className="inter-left accent-left">
			 <div className="bhoechie-tab11 tab-pane">
				<PerfectScrollbar>
					<ul className="bhoechie-tab-menu11 mCustomScrollbar int-list-2 clearfix" ref={this.ulRef}>
						{items}
					</ul>
				</PerfectScrollbar>
				{this.state.selectedStyle.styles ? <AccentSubStyles {...this.state} activeStyle={this.state.selectedStyle} selectedJacketStyles={this.props.selectedJacketStyles} updateProduct={(e)=>this.updateProduct(e)} frontViewImages={this.props.frontViewImages} backViewImages={this.props.backViewImages} foldViewImages={this.props.foldViewImages} categoryClick={(e)=>this.categoryClick(e)}/> : null}          
			  </div>
		 </div>
       )
   }
}

export default Accent;