import React from 'react';

class Save extends React.Component {

    dismissModal() {
        this.props.toggle(this.props.showSaveModal);
    }
    render(){
       return (
        <div onClick={this.dismissModal.bind(this)} className={`modal fade ${this.props.showSaveModal ?'in show':''}`} stye={{ display: `${this.props.showModal ?'block':'none'}`}} id="Save-my-design" tabIndex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 className="modal-title" id="myModalLabel">Save Design</h4>
                    </div>
                    <div className="modal-body mCustomScrollbar">
                        <div className="copy-link save-de">
                            <div className="form-group">
                                <label >Design Name</label>
                                <input type="text" className="form-control"  placeholder="Design Name" />
                            </div>
                            <div className="form-group">
                                <label >Email address</label>
                                <input type="email" className="form-control"  placeholder="Email" />
                            </div>
                            <div className="form-group">
                                <label >Password</label>
                                <input type="Password" className="form-control"  placeholder="*********" />
                            </div>
                            <div className="apply-filter-btn"><a href="#">Save Design</a></div>
                        </div>
                    </div>
                    <div className="modal-footer">
                    <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                    </div>
                </div>
            </div>
        </div>
       )
   }
}

export default Save;