import React from 'react';
import Front from './Front';
import Back from './Back';
import Fold from './Fold';

class ToolHolder extends React.Component {

  constructor(props) {
    super(props);
    
    this.state = {
      actView:this.props.actView
    }
  }

  changeView(e){
    const actView = e.currentTarget.title;

    //trigger to parent comp func
    this.props.changeView(actView);
  }

  handleZoomAreaClose(){
    this.props.handleZoomAreaClose()
  }


  render(){
    const frontViewImages= this.props.frontViewImages;
    const backViewImages = this.props.backViewImages;
    const foldViewImages = this.props.foldViewImages;

    return (
      <React.Fragment>

        <div className="suit-preloader" style={{display: this.props.viewLoader}}>
            <div className="suit-preloader-inner">
                <img width="50" height="50" src="img/Idesignibuy_loader.gif"/>
            </div>
        </div>
        <div className="views">
            <span className="close-zoom" onClick={(e)=>this.handleZoomAreaClose(e)}><i className="fa fa-close"></i></span>
            <Front frontViewImages={frontViewImages} actView={this.props.actView} isMannquin={this.props.isMannquin} productType={this.props.productType}/>
            <Back backViewImages={backViewImages} actView={this.props.actView} isMannquin={this.props.isMannquin} productType={this.props.productType}/>
            <Fold foldViewImages={foldViewImages} actView={this.props.actView} productType={this.props.productType}/>
        </div>
        <div className="slide-vews-dot">
          <ul className="clearfix">
              <li><a title="Front" className={`${this.props.actView == 'Front'?'active':''}`} onClick={(e)=>this.changeView(e)}></a></li>
              <li><a title="Back" className={`${this.props.actView == 'Back'?'active':''}`} onClick={(e)=>this.changeView(e)}></a></li>
              <li><a title="Inside" className={`${this.props.actView == 'Inside'?'active':''}`} onClick={(e)=>this.changeView(e)}></a></li>
          </ul>
        </div>
      </React.Fragment>
     ) 
   }
}

export default ToolHolder;