import React from 'react';

class Share extends React.Component {

   dismissModal() {
       this.props.toggle();
   }

   render(){
       return (
        <div onClick={this.dismissModal.bind(this)} className={`modal fade ${this.props.showModal ?'in show':''}`} stye={{ display: `${this.props.showModal ?'block':'none'}`}} id="Share-my-design" tabIndex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <h4 className="modal-title" id="myModalLabel">Share via:</h4>
                    </div>
                    <div className="modal-body mCustomScrollbar">
                        <ul className="clearfix share-list">
                            <li className="share-facebook"><a href="#facebook"><i className="fa fa-facebook"></i> <span>Facebook</span></a></li>
                            <li className="share-twitter"><a href="#twitter"><i className="fa fa-twitter"></i> <span>Twitter</span></a></li>
                            <li className="share-pinterest"><a href="#pinterest"><i className="fa fa-pinterest-p"></i> <span>Pinterest</span></a></li>
                            <li className="share-email"><a href="#envelope"><i className="fa fa-envelope"></i> <span>Email</span></a></li>
                            <li className="share-linkedin"><a href="#linkedin"><i className="fa fa-linkedin"></i> <span>Linkedin</span></a></li>
                            <li className="share-whatsapp"><a href="#whatsapp"><i className="fa fa-whatsapp"></i> <span>What's app</span></a></li>
                        </ul>
                        <div className="copy-link">
                            <div className="form-group">
                                <input type="text" className="form-control"  placeholder="file:///Vishnu-Project-mac/07-OLD-DATA-Desktop/MY-WEB-TOOLS/my-tools/new-one-(own-suit)/html/index.html" disabled="disabled" />
                            </div>
                            <div className="apply-filter-btn"><a href="#">Copy Link</a></div>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                    </div>
                </div>
            </div>
        </div>
       )
   }
}

export default Share;