/*
This file contails function related to fabric rendering, filter fabric by search.
*/

import React from "react";

//import '../Filter/Filter.css';
import Filter from '../Filter/Filter';
import PerfectScrollbar from 'react-perfect-scrollbar';

class Fabric extends React.Component {
  constructor(prop){
    super(prop);

    this.state = {
      isSameFabric:true,
      openFilter: false,
      actFabricId: this.props.actFabricId,
      fabricList:this.props.Fabrics['fabric'],
      totFabrics:this.props.Fabrics['fabric'].length,
      //initialy need add fabric filter cat here
      passingTags:{
           "material": [],
           "pattern": [],
           "color": [],
           "collection": []
        }
      }
  }

  toggleFilter(e){
    this.setState({
      openFilter: !this.state.openFilter
    });
  }

  //update jacket, pant and vest fabric data
  updateJacketFabData(item){
    this.props.selectedJacketStyles['fabric']['actFabricId'] = item.id;
    this.props.selectedJacketStyles['fabric']['styleName'] = item.name;
    this.props.selectedJacketStyles['fabric']['img'] = item.fabric_360;
  }
  updatePantFabData(item){
    this.props.selectedPantStyles['fabric']['actFabricId'] = item.id;
    this.props.selectedPantStyles['fabric']['styleName'] = item.name;
    this.props.selectedPantStyles['fabric']['img'] = item.fabric_360;
  }
  updateVestFabData(item){
    console.log("dd = ",this.props.selectedVestStyles)
    this.props.selectedVestStyles['fabric']['actFabricId'] = item.id;
    this.props.selectedVestStyles['fabric']['styleName'] = item.name;
    this.props.selectedVestStyles['fabric']['img'] = item.fabric_360;
  }
  //end

  fabricClick(e,item){

    this.props.handleViewLoader();
    //review: update fabric data
    this.setState({
      actFabricId: item.id
    });
    
    console.log("podtyp = "+this.props.productType," actLft = ",this.props.actLeftNav," isSFab = ",this.state.isSameFabric)

    if(this.props.productType === '3piece'){
      //jacket
      if(!this.state.isSameFabric && this.props.actLeftNav == "jacketTool")
      {
        this.updateJacketFabData(item);

      } else if(!this.state.isSameFabric && this.props.actLeftNav == "pantTool")//pant
      {
        this.updatePantFabData(item);

      } else if(!this.state.isSameFabric && this.props.actLeftNav == "vestTool")//vest
      {
        this.updateVestFabData(item);
      }else{
        this.updateJacketFabData(item);
        this.updatePantFabData(item);
        this.updateVestFabData(item);
      }
    }else if(this.props.productType === '2piece'){
      if(!this.state.isSameFabric && this.props.actLeftNav == "jacketTool")
      {
        this.updateJacketFabData(item);

      } else if(!this.state.isSameFabric && this.props.actLeftNav == "pantTool")
      {
        this.updatePantFabData(item);
      }else{
        this.updateJacketFabData(item);
        this.updatePantFabData(item);
      }

    }else if(this.props.productType === 'jacket'){
        this.updateJacketFabData(item);
    }else if(this.props.productType === 'pant'){
        this.updatePantFabData(item);
    }else if(this.props.productType === 'vest'){
        this.updateVestFabData(item);
    }

    //update fabric preview data : step 1
    this.props.fabricPreviewDetails['name'] = item.name;
    this.props.fabricPreviewDetails['img'] = item.fabric_360;

    //update update product:- 2
    this.props.updateProduct();
  }

  fabricView(e,item){

    //update fabric preview data : step 1
    this.props.fabricPreviewDetails['name'] = item.name;
    this.props.fabricPreviewDetails['img'] = item.fabric_360;

    //update fabric preview data : step 2
    this.props.handleFabricImageUpdate();
  }

  //Filter fabric list by search keyword(fabric name and type) and update state
  searchFNFabricList(e)
  {

    let fabrics = this.props.Fabrics['fabric'].filter( function (item) {
      return item.name.toLowerCase().search(e.target.value.toLowerCase()) !== -1 || item.type.toLowerCase().search(e.target.value.toLowerCase()) !== -1;
    });

    //unselect slected filter checkboxs
    this.unSelectAllFilterOp();

    this.setState({
      fabricList: fabrics
    });

  }

  handleFilterListChkb(e){
      
      const filterCat = e.currentTarget.id.split("_")[0];
      const filterVal  = e.currentTarget.value;

      

      //Get the filter data and update filter on apply filter button
      this.setState(prevState => {
        const passingTags = {...prevState.passingTags};

        if(passingTags[filterCat].indexOf(filterVal) === -1) {

           passingTags[filterCat].push(filterVal);

        }else{
           var ind = passingTags[filterCat].indexOf(filterVal)
           
           passingTags[filterCat].splice(ind,1);
        }

        return { passingTags }
      })
      
  }

  handleApplyFilterBtn(){
    this.toggleFilter()
  
    //apply filter to fabric list
    this.setState({
      fabricList: this.filterPlainArray()
    });
  }

  handleClearFilterBtn(){
    
    this.toggleFilter()

    
    //unselect slected filter checkboxs
    this.unSelectAllFilterOp();


    //apply filter to fabric list
    this.setState({
      fabricList: this.filterPlainArray()
    });

   } 

  unSelectAllFilterOp() {

    const filterkeys = this.state.passingTags; 

    for (let i in filterkeys) {
      filterkeys[i] = [];
    }

    //clear applied filters
    this.setState({
      passingTags: filterkeys
    });


    var textinputs = document.querySelectorAll('input[type=checkbox]'); 
    var items = [].filter.call( textinputs, function( el ) {
      return el.checked
    });

    for (var i = 0; i < items.length; i++) {
        items[i].checked = false;
    }

  }	

  //took input as fabric array and filter attribute array and return filterd fabric list
  filterPlainArray() {

    let selected;

    const filterValues = this.state.passingTags; 
    const fabrics = this.props.Fabrics['fabric'];

    //reset input text filter
    document.getElementById("searchFabric").value="";

    //material
    let filterAfterMaterials = [];
    selected = false;
    for (let j in fabrics) {
        let p = fabrics[j];
        
        for (let i in filterValues['material']) {
            if (filterValues['material'][i]) {
                selected = true;
                if (filterValues['material'][i] === p.material_parent) {
                    filterAfterMaterials.push(p);
                    break;
                }
            }
        }
    }
    if (!selected) {
        filterAfterMaterials = fabrics;
    }

    //pattern
    let filterAfterPatterns = [];
    selected = false;
    for (let j in filterAfterMaterials) {
        let p = filterAfterMaterials[j];
        for (let i in filterValues['pattern']) {
            if (filterValues['pattern'][i]) {
                selected = true;
                if (filterValues['pattern'][i] === p.pattern_parent) {
                    filterAfterPatterns.push(p);
                    break;
                }
            }
        }
    }
    if (!selected) {
        filterAfterPatterns = filterAfterMaterials;
    }
    
    //color
    let filterAfterColors = [];
    selected = false;
    for (let j in filterAfterPatterns) {
        let p = filterAfterPatterns[j];
        for (let i in filterValues['color']) {
            if (filterValues['color'][i]) {
                selected = true;
                if (filterValues['color'][i] === p.color_parent) {
                    filterAfterColors.push(p);
                    break;
                }
            }
        }
    }
    if (!selected) {
        filterAfterColors = filterAfterPatterns;
    }


    let filterAfterCollection = [];
    selected = false;
    for (let j in filterAfterColors) {
        let p = filterAfterColors[j];
        for (let i in filterValues['collection']) {
            if (filterValues['collection'][i]) {
                selected = true;
                if (filterValues['collection'] === p.collection_parent) {
                  filterAfterCollection.push(p);
                    break;
                }
            }
        }
    }
    if (!selected) {
      filterAfterCollection = filterAfterColors;
    }

    return filterAfterCollection;
  }

  removeFilterItem(e,parent,item){
    
    //remove specific ele and update filter data
    this.removeElementFList(parent,item)

    //update filter
    this.setState({
      fabricList: this.filterPlainArray()
    });
  }
   
  removeElementFList(filterCat,filterVal) {
    
    const filterkeys = this.state.passingTags; 

    let ind = filterkeys[filterCat].indexOf(filterVal)
   
    filterkeys[filterCat].splice(ind, 1);
   

    //clear applied filters
    this.setState({
      passingTags: filterkeys
    });

    //uncheck specific input
    document.querySelector('input[value="'+filterVal+'"]').checked = false;
  }

  handleSameFabric(e){
     //console.log("e = ",e.currentTarget.checked)
     this.setState({
        isSameFabric: !this.state.isSameFabric
     })
  }

  
  render(){
    const openFilter = this.state.openFilter;
    const filtredFabLen = this.state.fabricList.length;
    let fabItems;
    //filter on close
    const filterValues = this.state.passingTags;

    let actFabricId = this.state.actFabricId;
    if(!this.state.isSameFabric && this.props.actLeftNav == "jacketTool")
    {
      actFabricId = this.props.selectedJacketStyles['fabric']['actFabricId'];

    } else if(!this.state.isSameFabric && this.props.actLeftNav == "pantTool")//pant
    {
      actFabricId = this.props.selectedPantStyles['fabric']['actFabricId'];
    } else if(!this.state.isSameFabric && this.props.actLeftNav == "vestTool")//vest
    {
      actFabricId = this.props.selectedVestStyles['fabric']['actFabricId'];
    }

    //material
    const materailItems = filterValues['material'].map((item) => {
      return (
        <li key={item}>
          <span className="clearfix">	
          Material-{item}
            <a onClick={(e)=>this.removeFilterItem(e,'material',item)}><i className="fa fa-close"></i></a>
          </span> 
        </li>
      )
    });

    //pattern
    const patternItems = filterValues['pattern'].map((item) => {
      return (
        <li key={item}>
          <span className="clearfix">	
          Pattern-{item}
            <a onClick={(e)=>this.removeFilterItem(e,'pattern',item)}><i className="fa fa-close"></i></a>
          </span> 
        </li>
      )
    });

    //color
    const colorItems = filterValues['color'].map((item) => {
      return (
        <li key={item}>
          <span className="clearfix">	
          Color-{item}
            <a onClick={(e)=>this.removeFilterItem(e,'color',item)}><i className="fa fa-close"></i></a>
          </span> 
        </li>
      )
    });

    //collection
    const collnItems = filterValues['collection'].map((item) => {
      return (
        <li key={item}>
          <span className="clearfix">	
          Collection-{item}
            <a onClick={(e)=>this.removeFilterItem(e,'collection',item)}><i className="fa fa-close"></i></a>
          </span> 
        </li>
      )
    });
    //filter on close end
    
    //fabric data mapping start
    if(this.state.fabricList.length){

      fabItems = this.state.fabricList.map((item) => {

        return (
              <li key={item.id} className={`${actFabricId === item.id?'active':''}`}>
                <a  className="clearfix">	
                  <span className="fab-img-div">
                      <img src={item.fabric_360} alt="fabric-thumb" onClick={(e)=>this.fabricClick(e,item)}/> 
                      {/* <span className="new-lab">New</span> */}
                      <span className="fabric-view-ico fabric-frame" onClick={(e)=>this.fabricView(e,item)}>
                         <img src="img/search-icon.png"/>
                      </span>
                  </span>
                  <h5 className="clearfix">{item.name} <span className="pull-right">${item.price}</span></h5>
                  <p>{item.type}</p>
                </a> 
              </li>
            )
        });

    }else{
      fabItems = <li><a>No search result found.</a></li>
    }
    //fabric data mapping end
    

    return (
      <div className={`${openFilter?'open-fabric':''} inter-left fabric-left`}>
        <div className="head-title">Fabrics ({filtredFabLen}) &nbsp;&nbsp;
           <a style={{ display: `${(this.props.productType==="3piece" || this.props.productType==="2piece")?'inline-block':'none'}`}}> <input type="checkbox" id="sameFabric" onChange={(e)=>this.handleSameFabric(e)} checked={this.state.isSameFabric}/><label htmlFor="sameFabric">Same Fabric</label></a>
           <a className={`${openFilter?'open-fabric-scale':''} filter-a`} onClick={(e)=>this.toggleFilter(e)}><i className="fa fa-filter"></i></a>
        </div>

        <div className="fab-searching">
            <div className="input-group filter-items">
                {materailItems}
                {patternItems}
                {colorItems}
                {collnItems}
            </div>
            <div className="input-group">
                <input type="text" className="form-control" id="searchFabric" placeholder="Search Fabric" onChange={(e)=>this.searchFNFabricList(e)}/>
                <span className="input-group-btn">
                <button className="btn btn-default" type="button"><i className="fa fa-search"></i></button>
                </span>
            </div>
        </div>

        <ul className="style-fabrics-list mCustomScrollbar clearfix">
          <PerfectScrollbar>
             {fabItems}
          </PerfectScrollbar>
        </ul>

        <Filter fabricsCategory = {this.props.Fabrics['category']} handleFilterListChkb={(e)=>this.handleFilterListChkb(e)} handleApplyFilterBtn={(e)=>this.handleApplyFilterBtn(e)} handleClearFilterBtn={(e)=>this.handleClearFilterBtn(e)} totFabrics={this.state.totFabrics} filtredFabLen={filtredFabLen}/>
      </div>
    );
  }
}

export default Fabric;