import React from 'react';
import SubStyles from '../SubStyles/SubStyles';
// import './Jacket.css';
import PerfectScrollbar from 'react-perfect-scrollbar';


class Jacket extends React.Component {
  constructor(props) {

    super(props);
    this.ulRef = React.createRef();//creating parent element ref inside the constructor:
    this.state = {
      jacketStyles: props.jacketStyles,
      selectedIndex: null,
      passRef:''
    }
  }

  categoryClick(e, item,i) {
    
    this.ulRef.current.classList.add('sml-nav-2');//adding class to parent element

    this.setState({
      passRef: this.ulRef,
      activeStyle: item,
      selectedIndex : item.id
     })
    
    //trigger to parent comp fun
    this.props.categoryClick(item);
  }

  updateProduct(){
    //trigger to parent com fun
    this.props.updateProduct();
  }

  render() {

    const actLeftNav = this.props.actLeftNav;
    const jKStyles = this.props.jacketStyles;
    
    const styles = jKStyles.map((item,i) => {
      return (
         <li onClick={(e) => this.categoryClick(e,item,i)}  key = {i}><a className={this.state.selectedIndex===item.id ? 'active': ''}><i className={item.class}></i> <span>{item.name}</span></a></li>
      )
    });
    
    // This syntax ensures `this` is bound within handleClick
    return (
      <div role="tabpanel" className={`${actLeftNav === 'jacketTool' ?'fade in active':'fade out'} tab-pane bhoechie-tab1`}  id="jacket-link">
        <PerfectScrollbar>
        <ul className="bhoechie-tab-menu1 int-list-2 clearfix" ref={this.ulRef}>
          
            {styles}
          
        </ul>
        </PerfectScrollbar>
          {this.state.activeStyle?<SubStyles activeStyle={this.state.activeStyle} passRef={this.state.passRef} selectedJacketStyles={this.props.selectedJacketStyles} updateProduct={(e)=>this.updateProduct(e)} frontViewImages={this.props.frontViewImages} backViewImages={this.props.backViewImages} foldViewImages={this.props.foldViewImages}/>:null}
      </div>
    );
  }
}

export default Jacket;