
import React from 'react';
//import './SubStyle.css';
import PerfectScrollbar from 'react-perfect-scrollbar';

class SubStyles extends React.Component {
  constructor(props){    
    super(props);
    this.chieldUlRef = React.createRef();
    this.state={
      activeStyle : this.props.activeStyle,
      menuStyle: this.props.passRef,
      activeIndex: ''
    } 
  }

  designClick(e, item,i) {

    this.setState({activeIndex: i}) 

    if(item['designType'] === 'jacket'){

      this.props.selectedJacketStyles[item['parent']]['styleName'] = item['name'];
      this.props.selectedJacketStyles[item['parent']]['class'] = item['class'];

    }else if(item['designType'] === 'pant'){

      this.props.selectedPantStyles[item['parent']]['styleName'] = item['name'];
      this.props.selectedPantStyles[item['parent']]['class'] = item['class'];

    }else{

      this.props.selectedVestStyles[item['parent']]['styleName'] = item['name'];
      this.props.selectedVestStyles[item['parent']]['class'] = item['class'];

    }

    //update style ids  ==> Front view
    this.props.frontViewImages[0]['viewimages'].map((img,i) => {
      //change style :- step 1
      console.log("item = ",item)
      if(item['parent'] === img['rel']){
        
          img.activeStyle = item.id;
      }
      
      if(item['parent'] === img['rel'] && img['rel'] === "style"){
          img.activeStyle = item.type;
      }

      //style lapel
      if(item['parent'] === "lapel" && (img['rel'] === 'lapelupper' || img['rel'] === 'lapellower')){
        
        img.activeStyle = item.id;
      }

      //change lapel when style is active :- step 2
      if(item['parent'] === "style" && (img['rel'] === 'lapelupper' || img['rel'] === 'lapellower')){
          img.actStyleType = item.type;
      }

      //change button when style is active :- step 3
      if(item['parent'] === "style" && (img['rel'] === 'buttons' || img['rel'] === "threads")){
        img.actStyleType = item.type;
        img.activeStyle = item.id;//butoon count  = style id
        
      }

      //vest buttons
      if(item['parent'] === "veststyle" && img['rel'] === 'vestbuttons'){
        img.actStyleType = item.style_id;
        img.activeStyle = item.id;//butoon count  = style id
      }

      //vest style
      if(item['parent'] === "veststyle" && img['rel'] === 'vestedge'){
        img.actStyleType = item.style_id;
        // img.activeStyle = item.id;
      }
      //vest lapel 
      if(item['parent'] === "veststyle" && img['rel'] === 'vestlapel'){
        console.log("item = ",item)
        img.actStyleType = item.style_id;
        // img.activeStyle = item.id;
      }
    });

    //update style ids  ==> Fold view 
    this.props.foldViewImages[0]['viewimages'].map((img,i) => {

      //change style :- step 1
      if(item['parent'] === img['rel']){
          img.activeStyle = item.id;
      }
      
      if(item['parent'] === img['rel'] && img['rel'] === "style"){
          img.activeStyle = item.type;
      }

      //change lapel when style is active :- step 2
      if(item['parent'] === "style" && (img['rel'] === 'lapelupper' || img['rel'] === 'lapellower')){
          img.actStyleType = item.type;
      }

      //change button when style is active :- step 3
      if(item['parent'] === "style" && (img['rel'] === 'buttons' || img['rel'] === "threads")){
        img.actStyleType = item.type;
        img.activeStyle = item.id;//butoon count  = style id
        
      }

    });

    //update style ids  ==> back view 
    this.props.backViewImages[0]['viewimages'].map((img,i) => {
      //change style :- step 1
      if(item['parent'] === img['rel']){
          img.activeStyle = item.id;
      }
      
      if(item['parent'] === img['rel'] && img['rel'] === "style"){
        img.activeStyle = item.type;
      }

      if(item['parent'] === "sleevebuttons" && img['rel'] === "sleevethreads"){
        img.activeStyle = item.id;
      }

    });

    //tigger to parent component, update update product/views
    this.props.updateProduct();
  }

  handleRemover = (e) => {
    this.state.menuStyle.current.classList.remove('sml-nav-2');
    this.chieldUlRef.current.classList.remove('active');
  }
  
  componentDidMount = () => {
    this.chieldUlRef.current.classList.add('active');
    const subItem = this.props.activeStyle;
    this.setState({activeStyle:subItem.style});
  } 

  componentDidUpdate = () => {
    this.state.menuStyle.current.classList.add('sml-nav-2');
    this.chieldUlRef.current.classList.add('active');
  }

  render() {

    const subItem=this.props.activeStyle; 
    const items = subItem.style.map((item,i) => {

      if(item['designType'] === 'jacket'){
        return (
          <li onClick={(e) => this.designClick(e, item,i)} key={i} ><a className={`${this.props.selectedJacketStyles[item['parent']]['styleName'] === item['name']?'active':''}`}><i className={` ${item.class}`} ></i> <span>{item.name}</span></a></li>
        )
      }else if(item['designType'] === 'pant'){
        return (
          <li onClick={(e) => this.designClick(e, item,i)} key={i} ><a className={`${this.props.selectedPantStyles[item['parent']]['styleName'] === item['name']?'active':''}`}><i className={` ${item.class}`} ></i> <span>{item.name}</span></a></li>
        )
      }else{
        return (
          <li onClick={(e) => this.designClick(e, item,i)} key={i} ><a className={`${this.props.selectedVestStyles[item['parent']]['styleName'] === item['name']?'active':''}`}><i className={` ${item.class}`} ></i> <span>{item.name}</span></a></li>
        )
      }
    });
    
    return(   
      <div className={ this.props.activeStyle ? "bhoechie-tab-content1 in-tab-selects" : " " } ref={this.chieldUlRef}>
        <PerfectScrollbar>
        <h4 className="clearfix">{subItem.name} <span className="pull-right remover-class"  onClick={this.handleRemover} ><i className="icon-icon-113"></i></span></h4>
        
        
          <ul className="clearfix">
            {items}
          </ul>
        </PerfectScrollbar>
      </div>
    );
  }
}

export default SubStyles;