import React from 'react';
class PageLoader extends React.Component {
   render(){

       return (
        <div className="preloader" id="theInitialProgress" style={{display: this.props.pload}}>
            <div className="progressInner">
                <img width="70" height="70" src="img/Idesignibuy_loader.gif" />
                <p className="loaderText">Please wait while application configuration is loading....</p>
            </div>
        </div>
        )
   }
}

export default PageLoader;