import React from 'react';
import SubStyles from '../SubStyles/SubStyles';
import './Pant.css';
import PerfectScrollbar from 'react-perfect-scrollbar';


class Pant extends React.Component {
  constructor(props) {
    super(props);
    this.ulRef = React.createRef();//creating parent element ref inside the constructor:

    console.log("in pant")

    this.state = {
      pantStyles: this.props.pantStyles,
      selectedStyles: {},
      isActive:true,
      selectedIndex: null,
      passRef:''
    }
  }

  categoryClick(e, item,i) {
    console.log(e.target.parentElement.className)

    this.ulRef.current.classList.add('sml-nav-2');//adding class to parent element
    this.setState({
      activeStyle: item,
      isActive:true,
      selectedIndex : item.id,
      passRef:this.ulRef
    }) 

    //trigger to parent comp fun
    this.props.categoryClick(item);
  }

  updateProduct(){
    //trigger to parent com fun
    this.props.updateProduct();
  }
  

  render() {
    const actLeftNav = this.props.actLeftNav;    
    const pTStyles = this.state.pantStyles;
    const styles = pTStyles.map((item,i) => {
      return (
         <li onClick={(e) => this.categoryClick(e,item,i)}  key = {i} ><a className={this.state.selectedIndex===item.id ? 'active': ''}><i className={item.class}></i> <span>{item.name}</span></a></li>
      )
    });
    // This syntax ensures `this` is bound within handleClick
    return (
      <div role="tabpanel" className={`${actLeftNav == 'pantTool' ?'fade in active':'fade out'} tab-pane bhoechie-tab1`} id="pant-link">
         <PerfectScrollbar> 
        <ul className="bhoechie-tab-menu1 int-list-2 clearfix " ref={this.ulRef}>
         
              {styles}
          
        </ul>
        </PerfectScrollbar>
        {this.state.activeStyle?<SubStyles activeStyle={this.state.activeStyle} passRef={this.state.passRef} selectedPantStyles={this.props.selectedPantStyles} updateProduct={(e)=>this.updateProduct(e)} frontViewImages={this.props.frontViewImages} backViewImages={this.props.backViewImages} foldViewImages={this.props.foldViewImages}/>:null}
      </div>
    );
  }
}

export default Pant;