    handleProdInitData(){
        
        {/* product initialization */}
        if(this.state.productType === '2piece'){
            this.setState({
                selectedVestStyles:"",
                vestStyles:""
            });
        } else if(this.state.productType === 'jacket'){
            this.setState({
                selectedVestStyles:"",
                selectedPantStyles:"",
                vestStyles:"",
                pantStyles:""
            });
        } else if(this.state.productType === 'vest'){
            this.setState({
                selectedJacketStyles:"",
                selectedPantStyles:"",
                jacketStyles:"",
                pantStyles:"",
                actLeftNav:"vestTool",
                isJacket:false
            });
        }else if(this.state.productType === 'pant'){
            this.setState({
                selectedJacketStyles:"",
                selectedVestStyles:"",
                jacketStyles:"",
                vestStyles:"",
                actLeftNav:"pantTool",
                isJacket:false,
                upDownView:true
            });
        }

    }

    toggleModal(){
        this.setState({
            showShare:!this.state.showShare
        });
    }
    toggleSaveModal(){
        this.setState({
            showSave:!this.state.showSave
        });
    }
    toggleReviewModal(){
        this.setState({
            showReview:!this.state.showReview
        });
    }

    //fabric view Control:-this function is used to toggle fabric view page
    handleFabricDetailPage(){
        this.setState({
            showFabricPage:!this.state.showFabricPage
        });
    }

    //View Control (Up/Down) 
    handlePantUpDown(){
        this.setState({
            upDownView:!this.state.upDownView
        });
    }    

    //Controls Jacket hide shoe
    handleJacketHShow(){
        this.setState({isJacket:!this.state.isJacket}, () => {
            this.updateVestJacketView()
        });
    }

    updateVestJacketView(){

        this.state.frontViewImages[0]['viewimages'].map((item,i) => {
            if(item['type'] === 'jacket'){
                if(this.state.isJacket)
                {
                    this.state.frontViewImages[0]['viewimages'][i].isActive = true;
                }else{
                    this.state.frontViewImages[0]['viewimages'][i].isActive = false;
                }
                
            }else if(item['type'] === 'vest'){
                if(this.state.upDownView)
                {
                    this.state.frontViewImages[0]['viewimages'][i].isActive = false;
                }else{
                    this.state.frontViewImages[0]['viewimages'][i].isActive = true;
                }
                
            }
        });

        this.state.backViewImages[0]['viewimages'].map((itemb,ib) => {
            if(itemb['type'] === 'jacket'){
                if(this.state.isJacket)
                {
                    this.state.backViewImages[0]['viewimages'][ib].isActive = true;
                }else{
                    this.state.backViewImages[0]['viewimages'][ib].isActive = false;
                }
            }else if(itemb['type'] === 'vest'){
                if(this.state.upDownView)
                {
                    this.state.backViewImages[0]['viewimages'][ib].isActive = false;
                }else{
                    this.state.backViewImages[0]['viewimages'][ib].isActive = true;
                }
                
            }
        });

        this.state.foldViewImages[0]['viewimages'].map((itemb,ic) => {
            if(itemb['type'] === 'jacket'){
                if(this.state.isJacket)
                {
                    this.state.foldViewImages[0]['viewimages'][ic].isActive = true;
                }else{
                    this.state.foldViewImages[0]['viewimages'][ic].isActive = false;
                }
            }
        });


        //updated state in order to render update above changes..
        this.setState({
            isJacket:this.state.isJacket
        })
    }

    //Mannequin control hide/show
    handleMannquin(){
        this.setState({isMannquin:!this.state.isMannquin});
        if(!this.state.isMannquin){

            this.state.frontViewImages[0]['viewimages'][0].isActive = true;
            this.state.frontViewImages[0]['viewimages'][5].isActive = true;
            this.state.frontViewImages[0]['viewimages'][6].isActive = true;
            this.state.frontViewImages[0]['viewimages'][7].isActive = true;

            this.state.backViewImages[0]['viewimages'][0].isActive = true;
            this.state.backViewImages[0]['viewimages'][1].isActive = true;
            this.state.backViewImages[0]['viewimages'][2].isActive = true;

        }else{
            
            this.state.frontViewImages[0]['viewimages'][0].isActive = false;
            this.state.frontViewImages[0]['viewimages'][5].isActive = false;
            this.state.frontViewImages[0]['viewimages'][6].isActive = false;
            this.state.frontViewImages[0]['viewimages'][7].isActive = false;

            this.state.backViewImages[0]['viewimages'][0].isActive = false;
            this.state.backViewImages[0]['viewimages'][1].isActive = false;
            this.state.backViewImages[0]['viewimages'][2].isActive = false;
        }
    }
