    handleTopNav(e){
        this.setState({
            actTopNav:e.currentTarget.querySelector("a").text.toLowerCase()
        });
    }

    handleLeftNavComp(nav){ 
        this.setState({
          actLeftNav:nav
        });

        if(nav === "jacketTool"){
            this.setState({
                isJacket:true,
                upDownView: false
            },()=> this.updateVestJacketView())
        }else if(nav === "pantTool"){
            this.setState({
                isJacket:false,
                upDownView: true
            },()=> this.updateVestJacketView())
        }else{
            this.setState({
                isJacket:false,
                upDownView: false
            },()=> this.updateVestJacketView())
        }       
    }

    changeView(view){
    
        this.setState({
          actView:view
        });
    }

    categoryClick(item){
        
        
        if(item['name'] === 'sleeve buttons'|| item['name'] === 'vents')
        {
            this.setState({
                actView:"Back"
            });
        }else if(item['name'] === 'lining')
        {
            this.setState({
                actView:"Inside"
            });
        }else{
            this.setState({
                actView:"Front"
            });
        }
    }

    handleViewLoader(){
        this.setState({
            viewLoader:"block"
        });
    }

