/*
 This component has dependency of fabric component
*/
import React from 'react';

class Filter extends React.Component {

    constructor(prop){
        super(prop);
    
        this.state = {
          openFilterOption: ""
        }
    }

    toggleFilterOptions(e){
        let actText = e.currentTarget.text.toLowerCase();
       
        if(actText === this.state.openFilterOption){
            this.setState({
                openFilterOption: ""
            });
        }else{
            this.setState({
                openFilterOption: actText
            });
        }
    }

    handleFilterListChkb(e){
        this.props.handleFilterListChkb(e);
    }

    handleApplyFilterBtn(){
        this.props.handleApplyFilterBtn();
    }
    handleClearFilterBtn(){
        this.props.handleClearFilterBtn();
    }

   render(){

       const openFilterOption = this.state.openFilterOption;

       //fabric category
       let items = this.props.fabricsCategory.map((item,i) => {
        

        //child list
        let it = item.map(premise => (
            <li key={premise.id}><input type="checkbox" id={`${item[0].parent}_${premise.id}`} onChange={(e)=>this.handleFilterListChkb(e)} value={premise.name}/><label htmlFor={`${item[0].parent}_${premise.id}`}>{premise.name}</label></li>
        ));

        return (
            <li key={item[0].parent} className={`${openFilterOption === item[0].parent?'active':''}`}>
                <a  className="cust-plus-icon clearfix" onClick={(e)=>this.toggleFilterOptions(e)}>{item[0].parent}</a>
                <ul className={`${openFilterOption === item[0].parent?'active':''} sub-menu clearfix filter-checks`}>
                    {it}
                </ul>
            </li>
            )
        });  

       return (
            <div className="filter-div">
                <div className="head-title text-center">FILTERS</div>
                <div className="gallery-left-option">
                    <ul>
                        {items}
                    </ul>
                </div>

                <div className="apply-filter-btn">
                  <a className="apply-btn" onClick={(e)=>this.handleApplyFilterBtn(e)}>Apply Filters</a> 
                  <a className={`${this.props.totFabrics === this.props.filtredFabLen?'filter-active':''} apply-btn`} onClick={(e)=>this.handleClearFilterBtn(e)}>Clear Filters</a>
                </div>
            </div>
       )
   }
}

export default Filter;