import React from 'react';
import './AccentSubStyles';
import PerfectScrollbar from 'react-perfect-scrollbar';

class AccentSubStyles extends React.Component {
  constructor(props){ 
    super(props);
    
    this.chieldUlRef = React.createRef();

    this.state={
      styledata:this.props,
      activeStyle : this.props.activeStyle,
      menuStyle: props.passRef,
      activeIndex: ''
    }    
  }
  

  accentClick(e,item,i) {

      this.setState({activeIndex: i}) 

      this.props.selectedJacketStyles[item['parent']]['styleName'] = item['name'];
      this.props.selectedJacketStyles[item['parent']]['img'] = item['img'];

    //update style ids  ==> Front view
    this.props.frontViewImages[0]['viewimages'].map((img,i) => {
      
      //change BUTTON COLOR
      if(item['parent'] === img['rel']){
          img.actColorId = item.id;
      }

      //vest button color
      if(item['parent'] === "buttons" && img['rel'] === "vestbuttons"){
        img.actColorId = item.id;
      }

      //jacket and vest lining
      if(item['parent'] === "lining" && (img['rel'] === "lining" || img['rel'] === "vestlining")){
        img.actLiningFabricId = item.id;
      }

    });

    //update style ids  ==> back view
    this.props.backViewImages[0]['viewimages'].map((img,i) => {
      //change BUTTON COLOR
      if(item['parent'] === "buttons" && img['rel'] === "sleevebuttons"){
          img.actColorId = item.id;
      }

      //chnage sleeve thread color
      if(item['parent'] === "threads" && img['rel'] === "sleevethreads"){
        img.actColorId = item.id;
      }
    });

    //update style ids  ==> Fold view
    this.props.foldViewImages[0]['viewimages'].map((img,i) => {
      //change BUTTON COLOR
      if(item['parent'] === img['rel']){
          img.actColorId = item.id;
      }

       //vest button color
       if(item['parent'] === "buttons" && img['rel'] === "vestbuttons"){
        img.actColorId = item.id;
       }

       //jacket and vest lining
       if(item['parent'] === "lining" && (img['rel'] === "lining" || img['rel'] === "vestlining")){
           img.actLiningFabricId = item.id;
       }

    });

    //tigger to parent component, update update product/views
    this.props.updateProduct();
  } 

  updateProduct(){
    //trigger to parent comp fun
    this.props.updateProduct();
  }

  handleRemover = (e) => {
    this.state.menuStyle.current.classList.remove('sml-nav-2');
    this.chieldUlRef.current.classList.remove('active');
  }

  componentDidMount = () => {
    this.chieldUlRef.current.classList.add('active');
    const subItem = this.props.activeStyle;
    
    this.setState({activeStyle:subItem.style});
  } 

  componentDidUpdate = () => {
    this.state.menuStyle.current.classList.add('sml-nav-2');
    this.chieldUlRef.current.classList.add('active');
  }

  render() {
   
    let subItem = this.props.activeStyle;

    const items = subItem.styles.map((item,i) => {
        
      return (
          <li onClick={(e) => this.accentClick(e, item,i)} key = {i} className={`${this.props.selectedJacketStyles[item['parent']]['styleName'] == item['name']?'active':''}`}>
              <a className={`${item.class}`}>
              <img src={item.fabric_360} alt=""/>
              <h6 className="but-lab clearfix">{item.name} 
                 <span className="pull-right">${item.price}</span>
              </h6>
            </a>
          </li>
        )
    });
    
    return(   
      <div className={ this.props.activeStyle ? "bhoechie-tab-content1 in-tab-selects" : " " } ref={this.chieldUlRef}>
        <PerfectScrollbar>
          <h4 className="clearfix">{subItem.name} <span className="pull-right remover-class"  onClick={this.handleRemover} ><i className="icon-icon-113"></i></span></h4>
          <ul className="sub-menu button-list clearfix">
              {items}
          </ul>
        </PerfectScrollbar>
      </div>
    );
  }
}

export default AccentSubStyles;
