import React from 'react';

import Jacket from '../Jacket/Jacket';
import Pant from '../Pant/Pant';
import Vest from '../Vest/Vest';

class Style extends React.Component {
  constructor(props) { //called once
    super(props);

    this.state = {
      actLeftNav: this.props.actLeftNav
    }
  }

  handleLeftNavComp(e){
    const nav = e.currentTarget.className.split(/[\s,]+/);
    //trigger to parent component
    this.props.handleLeftNavComp(nav[0])
  }

  categoryClick(e){
    //trigger to parent comp fun
    this.props.categoryClick(e);
  }
  updateProduct(){
    //trigger to parent comp fun
    this.props.updateProduct();
  }
  
  render() {
      
    const jacketStyles= this.props.jacketStyles;
    const vestStyles = this.props.vestStyles;
    const pantStyles = this.props.pantStyles;

    return (   
      <div className="inter-left style-left">
        <div className="tab-mains">
            <ul className="left-style-nav-1 nav-tabs" role="tablist">
                {/* display jack left nav when actprodtype is not equal to pant and vest tool */}
                <li style={{ display: `${(this.props.productType !== 'pant') && (this.props.productType !== 'vest')?'block':'none'}`}}>
                  <a  onClick={(e) => this.handleLeftNavComp(e)} className={`jacketTool ${this.props.actLeftNav=='jacketTool'?'active':''}`}><i className="suit-icon-Single_Breasted_1_Button"></i> <span>Jacket</span></a>
                </li>
                {/* display pant left nav when actprodtype is not equal to jacket and vest tool */}
                <li style={{ display: `${(this.props.productType !== 'jacket') && (this.props.productType !== 'vest')?'block':'none'}`}}>
                  <a  onClick={(e) => this.handleLeftNavComp(e)} className={`pantTool ${this.props.actLeftNav=='pantTool'?'active':''}`}><i className="suit-icon-Pant_Regular_Fit"></i> <span>Pant</span></a>
                </li>
                {/* display vest left nav when actprodtype is 3piece and vest tool */}
                <li style={{ display: `${(this.props.productType === '3piece') || (this.props.productType === 'vest')?'block':'none'}`}}>
                  <a  onClick={(e) => this.handleLeftNavComp(e)} className={`vestTool ${this.props.actLeftNav=='vestTool'?'active':''}`}><i className="suit-icon-Vest_style_main"></i> <span>Vest</span></a>
                </li>
            </ul>
            
            <div className="tab-content">
                <Jacket jacketStyles={jacketStyles} actLeftNav={this.props.actLeftNav} selectedJacketStyles={this.props.selectedJacketStyles} updateProduct={(e)=>this.updateProduct(e)} frontViewImages={this.props.frontViewImages} backViewImages={this.props.backViewImages} foldViewImages={this.props.foldViewImages} categoryClick={(e)=>this.categoryClick(e)}/>
                <Pant pantStyles={pantStyles} actLeftNav={this.props.actLeftNav} selectedPantStyles={this.props.selectedPantStyles} updateProduct={(e)=>this.updateProduct(e)} frontViewImages={this.props.frontViewImages} backViewImages={this.props.backViewImages} foldViewImages={this.props.foldViewImages} categoryClick={(e)=>this.categoryClick(e)}/>
                <Vest vestStyles={vestStyles} actLeftNav={this.props.actLeftNav} selectedVestStyles={this.props.selectedVestStyles} updateProduct={(e)=>this.updateProduct(e)} frontViewImages={this.props.frontViewImages} backViewImages={this.props.backViewImages} foldViewImages={this.props.foldViewImages} categoryClick={(e)=>this.categoryClick(e)}/>
            </div>
        </div>
      </div>
    )
  }
}

export default Style;